import Operations from './Operations';
import defaults from '../defaults';
import Html5Builder from './Html5Builder';
import EventHandler from './EventHandler';
import EventListener from './EventListener';
import Helper from './Helper';

export default class ContextMenu {
    /**
     * @constructor
     * @constructs ContextMenu
     * @classdesc The ContextMenu is the core class that manages contextmenu's. You can call this class directly and skip going through jQuery.
     * @class ContextMenu
     *
     * @example
     * // You can call this class directly and skip going through jQuery, although it still requires jQuery to run.
     * const manager = new ContextMenu();
     * manager.execute("create", options);
     *
     * @property {ContextMenuOptions|Object} defaults
     * @property {EventHandler} handle
     * @property {Operations} operations
     * @property {Object<string, ContextMenuData>} menus
     * @property {number} counter - Internal counter to keep track of different menu's on the page.
     * @property {boolean} initialized - Flag the menu as initialized.
     */
    constructor() {
        this.html5builder = new Html5Builder();
        this.defaults = defaults;
        this.handler = new EventHandler();
        this.operations = new Operations();
        this.namespaces = {};
        this.initialized = false;
        this.menus = {};
        this.counter = 0;
    }

    /**
     * @method execute
     * @memberOf ContextMenu
     * @instance
     *
     * @param {(string|ContextMenuOptions)} operation
     * @param {(string|ContextMenuOptions)} options
     * @return {ContextMenu}
     */
    execute(operation, options) {
        const normalizedArguments = this.normalizeArguments(operation, options);
        operation = normalizedArguments.operation;
        options = normalizedArguments.options;

        switch (operation) {
            case 'update':
                // Updates visibility and such
                this.update(options);
                break;

            case 'create':
                // no selector no joy
                this.create(options);
                break;

            case 'destroy':
                this.destroy(options);
                break;

            case 'html5':
                this.html5(options);
                break;

            default:
                throw new Error('Unknown operation "' + operation + '"');
        }

        return this;
    }

    /**
     * if <menuitem> is not handled by the browser, or options was a bool true, initialize $.contextMenu for them.
     * @method html5
     * @memberOf ContextMenu
     *
     * @param {ContextMenuOptions|boolean} options
     */
    html5(options) {
        options = this.buildOptions(options);

        const menuItemSupport = ('contextMenu' in document.body && 'HTMLMenuItemElement' in window);

        if (!menuItemSupport || (typeof options === 'boolean' && options === true)) {
            $('menu[type="context"]').each(function () {
                if (this.id) {
                    $.contextMenu({
                        selector: '[contextmenu=' + this.id + ']',
                        items: $.contextMenu.fromMenu(this)
                    });
                }
            }).css('display', 'none');
        }
    }

    /**
     * Destroy the ContextMenu
     * @method destroy
     * @memberOf ContextMenu
     *
     * @param {ContextMenuOptions} options
     */
    destroy(options) {
        options = this.buildOptions(options);

        let $visibleMenu;

        // todo Context is deprecated in jQuery, perhaps just remove this way?
        if (options._hasContext) {
            // get proper options
            const context = options.context;

            Object.keys(this.menus).forEach((ns) => {
                let o = this.menus[ns];

                if (!o) {
                    return true;
                }

                // Is this menu equest to the context called from
                if (!$(context).is(o.selector)) {
                    return true;
                }

                $visibleMenu = $('.context-menu-list').filter(':visible');
                if ($visibleMenu.length && $visibleMenu.data().contextMenuRoot.$trigger.is($(o.context).find(o.selector))) {
                    EventListener.triggerEvent($visibleMenu.get(0), 'contextmenu:hide', {force: true});
                }

                this.removeListeners(o.ns);

                if (this.menus[o.ns].$menu) {
                    this.menus[o.ns].$menu.remove();
                }
                delete this.menus[o.ns];

                // todo JQuery?
                // $(o.context).off(o.ns);

                return true;
            });
        } else if (!options.selector) {
            // todo JQuery?
            // $(document).off('.contextMenu .contextMenuAutoHide');

            this.removeListeners();

            this.namespaces = {};
            this.menus = {};
            this.counter = 0;
            this.initialized = false;

            let layer = document.getElementById('context-menu-layer');
            if (layer) {
                layer.parentNode.removeChild(layer);
            }

            let contextMenus = document.querySelectorAll('.context-menu-list');
            contextMenus.forEach(function (e) {
                e.parentNode.removeChild(e);
            });
        } else if (this.namespaces[options.selector]) {
            let visibleMenus = this.getVisibleMenus();
            visibleMenus.forEach(function (e) {
                // todo Should we check for correct menu? $visibleMenu.data().contextMenuRoot.$trigger.is(options.selector)
                EventListener.triggerEvent(e, 'contextmenu:hide', {force: true});
            });

            let namespace = this.namespaces[options.selector];

            this.removeListeners(namespace);

            if (this.menus[namespace].$menu) {
                this.menus[namespace].$menu.remove();
            }
            delete this.menus[this.namespaces[options.selector]];

            // not needed
            // $(document).off(this.namespaces[options.selector]);
        }
        this.handler.$currentTrigger = null;
    }

    /**
     * Create a ContextMenu
     * @method create
     * @memberOf ContextMenu
     *
     * @param {ContextMenuOptions} options
     */
    create(options) {
        options = this.buildOptions(options);

        if (!options.selector) {
            throw new Error('No selector specified');
        }
        // make sure internal classes are not bound to
        if (options.selector.match(/.context-menu-(list|item|input)($|\s)/)) {
            throw new Error('Cannot bind to selector "' + options.selector + '" as it contains a reserved className');
        }
        if (!options.build && (!options.items || $.isEmptyObject(options.items))) {
            throw new Error('No Items specified');
        }
        this.counter++;
        options.ns = '.contextMenu' + this.counter;
        if (!options._hasContext) {
            this.namespaces[options.selector] = options.ns;
        }
        if (typeof options.listeners === 'undefined') {
            options.listeners = {
                document: new EventListener(document, options)
            };
        }

        this.menus[options.ns] = options;

        // default to right click
        if (!options.trigger) {
            options.trigger = 'right';
        }

        if (!this.initialized) {
            const itemClick = options.itemClickEvent === 'click' ? 'click' : 'mouseup';

            // make sure item click is registered first
            options.listeners.document
                .on('contextmenu:hide', '.context-menu-list', this.handler.hideMenu)
                .on('prevcommand', '.context-menu-list', this.handler.prevItem)
                .on('nextcommand', '.context-menu-list', this.handler.nextItem)
                .on('contextmenu', '.context-menu-list', this.handler.abortevent)
                .on('mouseleave', '.context-menu-list', this.handler.menuMouseleave)
                .on('mouseenter', '.context-menu-list', this.handler.menuMouseenter);

            options.listeners.document
                .on(itemClick, '.context-menu-item', this.handler.itemClick)
                .on('contextmenu:focus', '.context-menu-item', this.handler.focusItem)
                .on('contextmenu:blur', '.context-menu-item', this.handler.blurItem)
                .on('contextmenu', '.context-menu-item', this.handler.abortevent)
                .on('mouseleave', '.context-menu-item', this.handler.itemMouseleave)
                .on('mouseenter', '.context-menu-item', this.handler.itemMouseenter);

            options.listeners.document.on('mouseup', '.context-menu-input', this.handler.inputClick);

            this.initialized = true;
        }

        if (options._hasContext) {
            options.listeners.context = new EventListener(options.context.get(0), options);
        } else {
            options.listeners.context = options.listeners.document;
        }

        // engage native contextmenu event
        options.listeners.context.on('contextmenu', options.selector, this.handler.contextmenu);

        switch (options.trigger) {
            case 'hover':
                options.listeners.context
                    .on('mouseenter', options.selector, this.handler.mouseenter)
                    .on('mouseleave', options.selector, this.handler.mouseleave);
                break;

            case 'left':
                options.listeners.context.on('click', options.selector, this.handler.click);
                break;
            case 'touchstart':
                options.listeners.context.on('touchstart', options.selector, this.handler.click);
                break;
            /*
                     default:
                     // http://www.quirksmode.org/dom/events/contextmenu.html
                     $document
                     .on('mousedown' + o.ns, o.selector, o, this.handler.mousedown)
                     .on('mouseup' + o.ns, o.selector, o, this.handler.mouseup);
                     break;
                     */
        }

        // create menu
        if (!options.build) {
            this.operations.create(null, options);
        }
    }

    /**
     * Update the ContextMenu or all ContextMenu's
     * @method update
     * @memberOf ContextMenu
     *
     * @param {ContextMenuOptions} options
     */
    update(options) {
        options = this.buildOptions(options);

        if (options._hasContext) {
            this.operations.update(null, $(options.context).data('contextMenu'), $(options.context).data('contextMenuRoot'));
        } else {
            Object.keys(this.menus).forEach((menu) => {
                this.operations.update(null, this.menus[menu]);
            });
        }
    }

    /**
     * Build the options, by applying the Manager, defaults, user options and normalizing the context.
     * @method buildOptions
     * @memberOf ContextMenu
     *
     * @param {ContextMenuOptions} userOptions
     * @return {ContextMenuOptions}
     */
    buildOptions(userOptions) {
        if (typeof userOptions === 'string') {
            userOptions = {selector: userOptions};
        }

        const options = $.extend(true, {manager: this}, this.defaults, userOptions);

        if (!options.context || !options.context.length) {
            options.context = $(document);
            options._hasContext = false;
        } else {
            // you never know what they throw at you...
            options.context = $(options.context).first();
            options._hasContext = !$(options.context).is($(document));
        }
        return options;
    }

    /**
     * @method normalizeArguments
     * @memberOf ContextMenu
     *
     * @param {string|Object} operation
     * @param {string|Object|ContextMenuOptions} options
     * @returns {{operation: string, options: ContextMenuOptions}}
     */
    normalizeArguments(operation, options) {
        if (typeof operation !== 'string') {
            options = operation;
            operation = 'create';
        }

        if (typeof options === 'string') {
            options = {selector: options};
        } else if (typeof options === 'undefined') {
            options = {};
        }
        return {operation, options};
    }

    /**
     * import values into `<input>` commands
     *
     * @method setInputValues
     * @memberOf ContextMenu
     * @instance
     *
     * @param {ContextMenuData} contextMenuData - {@link ContextMenuData} object
     * @param {Object} data - Values to set
     * @return {undefined}
     */
    setInputValues(contextMenuData, data) {
        if (typeof data === 'undefined') {
            data = {};
        }

        $.each(contextMenuData.inputs, function (key, item) {
            switch (item.type) {
                case 'text':
                case 'textarea':
                    item.value = data[key] || '';
                    break;

                case 'checkbox':
                    item.selected = !!data[key];
                    break;

                case 'radio':
                    item.selected = (data[item.radio] || '') === item.value;
                    break;

                case 'select':
                    item.selected = data[key] || '';
                    break;
            }
        });
    }

    /**
     * export values from `<input>` commands
     *
     * @method getInputValues
     * @memberOf ContextMenu
     * @instance
     *
     * @param {ContextMenuData} contextMenuData - {@link ContextMenuData} object
     * @param {Object} data - Values object
     * @return {Object} - Values of input elements
     */
    getInputValues(contextMenuData, data) {
        if (typeof data === 'undefined') {
            data = {};
        }

        $.each(contextMenuData.inputs, function (key, item) {
            switch (item.type) {
                case 'text':
                case 'textarea':
                case 'select':
                    data[key] = item.$input.val();
                    break;

                case 'checkbox':
                    data[key] = item.$input.prop('checked');
                    break;

                case 'radio':
                    if (item.$input.prop('checked')) {
                        data[item.radio] = item.value;
                    }
                    break;
            }
        });

        return data;
    }

    /**
     * Triggers an event on the instance's element.
     *
     * @param {Element} el Element to trigger on
     * @param {string} eventName Name of the event to trigger.
     * @param {Object} data Optional event data to be added to the event object.
     * @param {boolean} bubbles
     * @param {boolean} cancelable
     *
     * @return {boolean} Whether the default action of the event may be executed, ie. returns false if preventDefault() has been called.
     */
    static triggerEvent(el, eventName, data = {}, bubbles = true, cancelable = true) {
        const event = new CustomEvent(eventName, {detail: data, bubbles: bubbles, cancelable: cancelable});
        el.dispatchEvent(event);
        return !event.defaultPrevented;
    }

    getVisibleMenus() {
        return Array.prototype.filter.call(document.querySelectorAll('.context-menu-list'), (element) => {
            return Helper.isVisible(element)
        });
    }

    /**
     * Removes all event listner
     *
     * @param {string?} namespace Namespace of the contextmenu to destroy, no value means all
     */
    removeListeners(namespace) {
        let namespaces = [namespace];
        if (!namespace) {
            namespaces = Object.values(this.namespaces);
        }

        namespaces.forEach((ns) => {
            if (this.menus[ns] && this.menus[ns].listeners) {
                Object.keys(this.menus[ns].listeners).forEach((key) => {
                    this.menus[ns].listeners[key].destruct();
                });
            }
        });
    }

    getMenuBySelector(selector){
        return Object.values(this.menus).find((menu) => menu.selector === selector);
    }
}
